import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider,
  Input,
  TreeSelect
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/inventoryHistoryReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import getLanguage from "../../../App/MyLanguange";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirect = {
  issue_receipt: "/dashboard/inventories/issue-receipts",
  purchase_receipt: "/dashboard/purchases/purchase-receipts",
  purchase_return: "/dashboard/purchases/purchase-returns",
  item_transfer: "/dashboard/inventories/item-transfers",
  sales_invoice: "/dashboard/sales/sales-invoices",
  sales_return: "/dashboard/sales/sales-returns",
  raptor_sales_invoice: "/dashboard/raptor/sales-invoices",
  raptor_sales_return: "/dashboard/raptor/sales-returns",
  pos_sales_invoice: "/dashboard/pos/sales-invoices",
  pos_sales_return: "/dashboard/pos/sales-returns",
  purchase_invoice: "/dashboard/purchases/purchase-invoices",
  delivery_order: "/dashboard/sales/delivery-orders",
  job_costing: "/dashboard/inventories/job-costings",
  job_rollover: "/dashboard/inventories/job-cost-rollovers",
  transfer_out: "/dashboard/inventories/item-transfers",
  transfer_in: "/dashboard/inventories/item-transfers"
};

const loop = array =>
  array.map(data => {
    let perData = {
      title: data.item_category_code + " (" + data.item_category_name + ")",
      value: data.id_item_category,
      key: data.id_item_category,
      children: data.children ? loop(data.children) : null
    };

    return perData;
  });

class InventoryHistoryReport extends Component {
  state = {
    filter: {
      id_item_category: null,
      id_location: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "transaction_date",
      sort_type: "asc",
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getInventoryHistoryReports } = this.props;
    getInventoryHistoryReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.inventoryHistoryReport.error) {
      notification("error", this.props.inventoryHistoryReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.inventoryHistoryReport.success) {
      notification("success", this.props.inventoryHistoryReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getInventoryHistoryReports } = this.props;
    let filter = this.filterValue();

    getInventoryHistoryReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getInventoryHistoryReports } = this.props;
    this.setState({
      filter: {
        id_item_category: null,
        id_location: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc",
        pagination: null
      },
      search: null
    });
    getInventoryHistoryReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.status) {
      string += "status=" + filter.status;
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    if (filter.id_cash_bank) {
      string
        ? (string += "&id_cash_bank=" + filter.id_cash_bank)
        : (string += "id_cash_bank=" + filter.id_cash_bank);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_item_category) {
      string
        ? (string += "&id_item_category=" + filter.id_item_category)
        : (string += "id_item_category=" + filter.id_item_category);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getInventoryHistoryReports } = this.props;
    //const { current_page } = this.props.inventoryHistoryReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getInventoryHistoryReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getInventoryHistoryReports } = this.props;
    this.setState({
      filter: {
        id_item_category: null,
        id_location: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc",
        pagination: null
      }
    });
    getInventoryHistoryReports();
  };

  render() {
    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "transactionDate" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        date: true,
        render: (text, row) => {
          if (row.transaction_type_string === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
        }
      },
      {
        title: getLanguage({ id: "transactionType" }),
        dataIndex: "transaction_type_string",
        rowKey: "transaction_type_string",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 7,
        render: (text, row) => {
          if (row.transaction_type_string === "Total") {
            return {
              children: <strong style={{ marginLeft: "50%" }}>{text}</strong>,
              props: {
                colSpan: 7
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.transaction_type_string === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return (
            <Link to={`${redirect[row.redirect]}/update/${row.id_transaction}`}>
              {text}
            </Link>
          );
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.transaction_type_string === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code",
        rowKey: "item_code",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.transaction_type_string === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        rowKey: "item_name",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.transaction_type_string === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      // {
      //  title: getLanguage({id: "sidebar.location"}),
      //   dataIndex: "location_name",
      //   rowKey: "location_name",
      //   render: (text, row) => {
      //     if (row.transaction_type_string === "Total") {
      //       return {
      //         props: {
      //           colSpan: 0
      //         }
      //       };
      //     }

      //     return text;
      //   }
      // },
      {
        title: getLanguage({ id: "price" }),
        dataIndex: "item_price",
        rowKey: "item_price",
        align: "right",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        parse: true,
        render: (text, row) => {
          if (row.transaction_type_string === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "qty" }),
        dataIndex: "qty_change",
        rowKey: "qty_change",
        align: "right",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 1,
        parse: true,
        render: (text, row) => {
          if (row.transaction_type_string === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "cost" }),
        dataIndex: "item_cost",
        rowKey: "item_cost",
        align: "right",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 1,
        parse: true,
        render: (text, row) => {
          if (row.transaction_type_string === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      }
    ];

    // Data setup
    const {
      inventory_history_reports,
      locations,
      item_categories,
      filter,
      filter_select
    } = this.props.inventoryHistoryReport;
    //console.log(this.props.inventoryHistoryReport.base_currency); //console.log(this.state);
    const dataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    let total_qty = 0;
    let total_cost = 0;
    inventory_history_reports.map((data, key) => {
      total_qty += parseFloat(data.qty_change);
      total_cost += parseFloat(data.item_cost);

      let string;
      let redirect;
      switch (data.transaction_type) {
        case 1:
          string = getLanguage({ id: "sidebar.issueReceipt" });
          redirect = "issue_receipt";
          break;
        case 3:
          string = getLanguage({ id: "sidebar.purchaseReceipts" });
          redirect = "purchase_receipt";
          break;
        case 4:
          string = getLanguage({ id: "sidebar.purchaseReturns" });
          redirect = "purchase_return";
          break;
        case 5:
          string = getLanguage({ id: "sidebar.itemTransfer" });
          redirect = "item_transfer";
          break;
        case 6:
          string = getLanguage({ id: "sidebar.salesInvoice" });
          redirect = "sales_invoice";
          break;
        case 7:
          string = getLanguage({ id: "sidebar.salesReturn" });
          redirect = "sales_return";
          break;
        case 10:
          string = getLanguage({ id: "sidebar.purchaseInvoices" });
          redirect = "purchase_invoice";
          break;
        case 11:
          string = getLanguage({ id: "sidebar.deliveryOrder" });
          redirect = "delivery_order";
          break;
        case 12:
          string = getLanguage({ id: "sidebar.jobCosting" });
          redirect = "job_costing";
          break;
        case 13:
          string = getLanguage({ id: "sidebar.jobCostRollover" });
          redirect = "job_rollover";
          break;
        case 14:
          string = getLanguage({ id: "transferOut" });
          redirect = "item_transfer";
          break;
        case 15:
          string = getLanguage({ id: "transferIn" });
          redirect = "item_transfer";
          break;
        case 17:
          string = getLanguage({ id: "Raptor Sales Invoice" });
          redirect = "raptor_sales_invoice";
          break;
        case 18:
          string = getLanguage({ id: "Raptor Sales Return" });
          redirect = "raptor_sales_return";
          break;
        case 19:
          string = getLanguage({ id: "POS Sales Invoice" });
          redirect = "pos_sales_invoice";
          break;
        case 20:
          string = getLanguage({ id: "POS Sales Return" });
          redirect = "pos_sales_return";
          break;
        default:
          break;
      }

      const modifier = {
        transaction_type_string: string,
        redirect: redirect
      };

      dataSource.push({
        ...data,
        ...modifier
      });
    });

    dataSource.push({
      key: inventory_history_reports.length,
      transaction_type_string: "Total",
      qty_change: total_qty,
      item_cost: total_cost
    });

    const { loading, loadingForeign } = this.props.inventoryHistoryReport;

    const fileName = getLanguage({ id: "inventoryHistoryReports.pageHeader" });
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="inventoryHistoryReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="inventoryHistoryReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() =>
                (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              scale={0.45}
              title="inventoryHistoryReports.pageHeader"
            />

            <ExcelWrapper
              title="inventoryHistoryReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "transactionNo" }) +
                ", " +
                getLanguage({ id: "items.code" }) +
                ", " +
                getLanguage({ id: "itemName" }) //+ ", " +
                //getLanguage({id: "createdBy"})
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="items.itemCategory" />
                    </label>
                    <br />
                    <TreeSelect
                      placeholder={
                        <IntlMessages id="items.select.itemCategory" />
                      }
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_item_category"
                      )}
                      style={{ width: "100%" }}
                      allowClear={true}
                      showSearch={true}
                      treeNodeFilterProp="title"
                      dropdownStyle={{ maxHeight: 300, overflow: "auto" }}
                      treeData={loop(item_categories)}
                      treeDefaultExpandAll
                      loading={loading}
                      value={
                        this.state.filter.id_item_category
                          ? this.state.filter.id_item_category
                          : []
                      }
                    />
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transaction_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option value="transaction_date">
                            <IntlMessages id="inventoryHistoryReports.filter.date" />
                          </Option>
                          <Option value="transaction_no">
                            <IntlMessages id="transactionNo" />
                          </Option>
                          <Option value="item_code">
                            <IntlMessages id="itemCode" />
                          </Option>
                          <Option value="item_name">
                            <IntlMessages id="itemName" />
                          </Option>
                          <Option value="location_name">
                            <IntlMessages id="locations.pageHeader" />
                          </Option>
                          <Option value="item_cost">
                            <IntlMessages id="itemCost" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ paddingTop: "8px", marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) : inventory_history_reports.length === 0 &&
              !this.props.inventoryHistoryReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="inventoryHistoryReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable forceHeight"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    inventoryHistoryReport: state.inventoryHistoryReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  InventoryHistoryReport
);
